import React from 'react'

import Obfuscate from 'react-obfuscate'

import styled from 'styled-components'

import { rhythm } from '../utils/typography'

const IndexContainer = styled.div`
  padding: 1rem;
  max-width: 30rem;
`

const Line = styled.p`
  line-height: 3rem;
  font-size: 3rem;
  margin: ${rhythm(1.5)} 0;

  a {
    text-decoration: none;
    color: #72a0c1;
    font-size: 1rem;

    &:hover {
      color: #9cbcd3;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
`

class Index extends React.Component {
  render() {
    return (
      <IndexContainer>
        <Line>
          Contact me:
        </Line>
        <Line>
          <Obfuscate
            email="william.chiu@columbiaspectator.com"
          />
        </Line>
        <Line>
          <Obfuscate
            tel="tel:516-606-8013"
          />
        </Line>
        <br/>
        <Line>
          Social me:
        </Line>
        <Line>
          <a href="https://www.linkedin.com/in/william-chiu/">
            LinkedIn
          </a>
        </Line>        
        <Line>
          <a href="https://www.facebook.com/achillyblue">
            Facebook
          </a>
        </Line>
        <Line>
          <a href="https://github.com/WilliamChiu">
            GitHub
          </a>
        </Line>
        <Line>
          <a href="https://www.snapchat.com/add/chillyblue">
            Snapchat
          </a>
        </Line>
      </IndexContainer>
    )
  }
}

export default Index

exports.frontmatter = {
  title: "Contact",
  inChilly: true
}


